import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import { InitTextCellParams, ITooltipCell } from '../../../../types';
import { CSSProperties } from '../../../../types/styles';

export interface TextCellData {
    text: string;
    tooltip?: string;
}

export interface ITextCellParams extends ICellRendererParams, ITooltipCell {
    text?: (data: TextCellData) => string;
    color?: string;
    textStyles?: any | ((params: ICellRendererParams) => any);
    textClass?: string;
    textTransform?: (data: any, value: any) => string;
}

/**
 * @category Table Cell
 * @description A basic text cell component for displaying simple text content in data tables. Provides consistent typography and overflow handling.
 */
@Component({
    tag: 'atui-text-cell',
    shadow: false,
})
export class AtuiTextCellComponent implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() containerStyles: CSSProperties;
    @State() textStyles: CSSProperties;
    @State() textClass: string;
    @State() textValue: string;
    @State() params: InitTextCellParams;

    init(params: InitTextCellParams): void {
        const {
            size,
            color,
            textStyles,
            containerStyles,
            textClass,
            textTransform,
        } = params;

        this.textClass = textClass;
        this.containerStyles = containerStyles;
        this.params = params;

        if (typeof textStyles == 'function') {
            this.textStyles = textStyles(params);
        } else {
            this.textStyles = {
                fontSize: size || null,
                color: color || null,
                ...textStyles,
            };
        }

        this.textValue = this.getTextValue(params);

        if (textTransform) {
            this.textValue = textTransform(params.data, params.value);
        }
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: InitTextCellParams): boolean {
        this.textValue = this.getTextValue(params);
        return true;
    }

    // If you are using objects for the value, they will appear as [object Object].
    // This indicates that you may need to use a valueGetter (see ag-grid API),
    // with a different cell type - this one is designed for displaying a single string value.
    private getTextValue(params: ICellRendererParams): string {
        const { value } = params;

        if (value === '') {
            return '-';
        }
        if (value.constructor === Array) {
            return this.transferArrayValueToString(value);
        }

        return String(value);
    }

    private transferArrayValueToString(value: Array<string | number>): string {
        const compactValue = value.filter((val) => !!val);
        if (!compactValue.length) {
            return '-';
        }
        const textVal: string | number = compactValue.reduce((acc, current) => {
            acc = acc ? `${acc},${current}` : `${current}`;
            return acc;
        });
        return String(textVal);
    }

    render() {
        return (
            <Host class="align-center flex h-full">
                <atui-tooltip
                    position="right"
                    disabled={!this.params.generateTooltip}
                >
                    <span
                        slot="tooltip-trigger"
                        data-index={`column-${this.params.rowIndex}-data`}
                        style={this.textStyles}
                        class={`${this.textClass ?? ''} truncate`}
                        onClick={() => {
                            if (this.params.click)
                                this.params.click(this.params);
                        }}
                    >
                        {this.textValue}
                    </span>
                    {this.params.generateTooltip && (
                        <span
                            slot="tooltip-content"
                            class={`${this.params.tooltipClass ?? ''} leading-normal`}
                        >
                            {this.params.generateTooltip(this.params)}
                        </span>
                    )}
                </atui-tooltip>
            </Host>
        );
    }
}
